          .col-lg-7.order-lg-1
            p(data-aos="fade-left") La salud animal comprende el conjunto integral, continuo y articulado de acciones preventivas, sanitarias, diagnsticas, teraputicas y de manejo responsable, orientadas a preservar el bienestar fsico, fisiolgico, mental, emocional y comportamental de las mascotas. Su propsito fundamental es prevenir la aparicin de enfermedades, reducir la mortalidad evitable, limitar la propagacin de agentes infecciosos, mitigar riesgos zoonticos y proteger la salud pblica y el equilibrio ambiental.
        
            .bg-color-11.tarjeta.p-4(data-aos="fade-left")
