                  p.mb-0 El mdico veterinario, como profesional competente para la prevencin, diagnstico y manejo sanitario.
            p.mb-0 En Colombia, la salud animal constituye una obligacin legal, tica y social del tutor, sustentada en el reconocimiento jurdico de los animales como seres sintientes, lo cual implica el deber de brindarles condiciones adecuadas de vida, proteccin contra el sufrimiento evitable y acceso oportuno a la atencin sanitaria.
            p Este deber se articula con el enfoque de Una Sola Salud (One Health), que reconoce la interdependencia entre la salud animal, la salud humana y la salud de los ecosistemas, especialmente frente a enfermedades zoonticas, resistencia antimicrobiana, contaminacin ambiental y emergencias sanitarias.


