      .col-lg-7.order-lg-1
        p(data-aos="fade-left") La medicina preventiva constituye el eje central y prioritario de la salud animal, ya que se fundamenta en la anticipacin a la enfermedad mediante planes sanitarios estructurados, continuos y adaptados a cada especie, individuo y contexto. Su objetivo principal es reducir la incidencia de enfermedades infecciosas, parasitarias, metablicas, nutricionales y zoonticas, as como mejorar la calidad, expectativa y dignidad de vida de las mascotas.
