          .col-lg
            .bg-color-1.tarjeta.p-4(data-aos="fade-left")
              p.mb-0 La medicina preventiva no solo disminuye los costos econmicos y emocionales asociados a la enfermedad, sino que reduce el sufrimiento animal, fortalece el bienestar integral y protege a la familia y a la comunidad frente a riesgos sanitarios.
    
