              p Permiten detectar de manera temprana alteraciones de salud, enfermedades subclnicas, problemas dentales, nutricionales, dermatolgicos o comportamentales. La frecuencia vara segn la especie, la edad y el estado sanitario.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
