                img(src="@/assets/curso/tema2/7.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Programas de vacunacin y desparasitacin
              p Se disean de acuerdo con la especie, la edad, el riesgo epidemiolgico y la normativa vigente. En perros y gatos incluyen esquemas definidos; en especies no convencionales se aplican solo bajo criterio profesional o se sustituyen por medidas estrictas de bioseguridad.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
