                img(src="@/assets/curso/tema2/8.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Control nutricional y del estado corporal
              p La alimentacin balanceada y especfica por especie previene enfermedades metablicas, deficiencias nutricionales, obesidad, inmunosupresin y trastornos digestivos.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/9.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
