              p Comprende la limpieza y desinfeccin de camas, jaulas, acuarios, bebederos, comederos y reas de descanso, lo que reduce la carga microbiana y parasitaria del ambiente.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
