            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Control reproductivo responsable
              p La esterilizacin y el manejo reproductivo tico previenen camadas no deseadas, disminuyen enfermedades reproductivas, comportamientos indeseados y el abandono animal.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
