        .row.align-items-center.p-4
          .col-lg-7.mb-4.mb-md-0
            h4 Medicina preventiva en mascotas no convencionales
            p En mascotas no convencionales (como aves ornamentales, cobayos, conejos, peces y otras especies permitidas) la medicina preventiva se basa principalmente en:
            ul.lista-ul--color.mb-0
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Manejo ambiental adecuado, respetando las necesidades biolgicas y etolgicas de cada especie..
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0  Bioseguridad estricta, con cuarentena de nuevos individuos y control del origen.
              li.mb-0.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Nutricin especfica por especie, evitando dietas inadecuadas o improvisadas.
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Observacin clnica constante, ya que estas especies tienden a ocultar los signos de enfermedad.
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0  Intervencin veterinaria especializada, dado que muchas especies no cuentan con esquemas vacunales definidos o con frmacos autorizados para uso rutinario.
            p En estos animales, la prevencin es especialmente crtica, ya que la deteccin tarda de la enfermedad suele asociarse con altas tasas de mortalidad.
