            .bg-color-8.tarjeta.p-4(data-aos="fade-left")
              p.mb-0 Las consideraciones especiales en salud felina se relacionan con la gravedad y el impacto de ciertas enfermedades: la panleucopenia felina presenta una alta tasa de mortalidad, las enfermedades respiratorias comprometen de manera significativa el bienestar del animal y la convivencia en el entorno familiar, y la aplicacin de la vacuna antirrbica se define segn el nivel de riesgo y las normativas locales vigentes.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.png")
        h3.titulo-con-imagen__texto Vacunacin en mascotas no convencionales
    
    p.mb-5 En las mascotas no convencionales, los programas vacunales son limitados o inexistentes, debido a:

    .row.justify-content-center.mb-3
      .col-lg-3.mb-3.mb-lg-0
        .bg-color-1.tarjeta
          .p-3
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema2/26.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 Diversidad biolgica.
      .col-lg-3.mb-3.mb-lg-0
        .bg-color-1.tarjeta
          .p-3
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema2/27.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 Falta de estudios de seguridad y eficacia.
      .col-lg-3.mb-3.mb-lg-0
        .bg-color-1.tarjeta
          .p-3
            .row.align-items-center
              .col-lg-auto.mb-3.mb-lg-0
                img(src="@/assets/curso/tema2/28.svg", style="max-width: 90px").mx-auto
              .col-lg
                p.mb-0 Falta de estudios de seguridad y eficacia.
    
    p.mb-5 A continuacin se presentan  las principales pautas preventivas y consideraciones sanitarias aplicables a aves ornamentales, pequeos mamferos y peces ornamentales, junto con criterios generales de seguridad vacunal.

    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-white.tarjeta.p-4
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/29.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Aves ornamentales
              p No se vacunan de manera rutinaria en tenencia domstica. La prevencin se sustenta en bioseguridad estricta, control sanitario del origen, cuarentena mnima de 30 das e higiene del alojamiento. La vacunacin en aves se limita a programas productivos y sanitarios oficiales.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/30.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Cobayos y conejos
              p En Colombia no existen esquemas vacunales estandarizados para animales de compaa. La prevencin depende del manejo sanitario adecuado, la higiene del entorno, la alimentacin balanceada y el control del estrs y del hacinamiento. La vacunacin solo se contempla bajo criterio veterinario especializado.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/31.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Peces ornamentales
              p No se aplican vacunas. El control sanitario se basa en la calidad del agua (pH, temperatura, amonio y nitritos), la densidad poblacional adecuada, la nutricin balanceada, la cuarentena de nuevos ejemplares y la limpieza y desinfeccin del acuario.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/32.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Consideraciones generales de seguridad vacunal
              p Vacunar nicamente animales clnicamente sanos, respetar los intervalos entre dosis y refuerzos, mantener la cadena de fro, registrar cada vacuna en el carn sanitario, vigilar posibles reacciones postvacunales y nunca automedicar ni utilizar vacunas destinadas a otras especies.
    
    p.mb-5 La vacunacin hace parte de las obligaciones del tutor dentro de la tenencia responsable y del cumplimiento del marco normativo colombiano que protege la vida, el bienestar animal y la salud pblica. El incumplimiento de los esquemas obligatorios, especialmente de rabia, puede generar sanciones administrativas y riesgos epidemiolgicos.

    separador
    
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 2.3 Desparasitacin
    
    .row.mb-5
      .col-lg-4.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema2/33.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        p(data-aos="fade-left") La desparasitacin es una medida esencial, permanente y obligatoria de la medicina preventiva, orientada al control de parsitos internos (endoparsitos) y externos (ectoparsitos) que afectan de forma directa la salud, el bienestar fsico, el estado nutricional, el comportamiento y la longevidad de las mascotas, tanto convencionales como no convencionales.
    
        .bg-color-15.tarjeta.p-4(data-aos="fade-left")
          .row.align-items-start
            .col-lg-auto.mb-3.mb-lg-0
              img(src="@/assets/curso/tema2/34.svg", style="max-width: 90px").mx-auto
            .col-lg
              p.mb-0 Los parsitos tambin representan un riesgo para la salud pblica, debido a su potencial zoontico, su capacidad para contaminar el ambiente y su rol como vectores de agentes infecciosos. Un programa de desparasitacin correctamente estructurado permite:

    .bg-full-width.bg-color-12
      .px-4.px-md-5
        .row.justify-content-center.mb-5
          .col-lg-7
            .titulo-sexto.color-acento-contenido
              h5 Figura 1.
              span Control de parsitos 
            figure
              img(src="@/assets/curso/tema2/35.png", data-aos="zoom-in", alt="Diagrama sobre el control de parsitos internos y externos que incluye acciones como reducir la carga parasitaria, prevenir daos en rganos, evitar consecuencias clnicas, disminuir la transmisin de enfermedades y la contaminacin ambiental, y mejorar la calidad de vida y el bienestar del animal. ")

        .row.justify-content-center.mb-5
          .col-lg-8
            .tarjeta.bg-color-6.mb-5
              .px-4.px-md-5.py-4
                .row.align-items-center
                  .col-lg-auto.mb-3.mb-lg-0
                    img(src="@/assets/curso/tema2/36.svg", style="max-width: 90px").mx-auto
                  .col-lg
                    p.mb-0 La desparasitacin debe formar parte de un plan sanitario integral que incluya vacunacin, higiene del entorno, control reproductivo, educacin del tutor y seguimiento veterinario peridico.
        
        .titulo-con-imagen.mb-5(data-aos="fade-right")
          .titulo-con-imagen__fondo
            img.titulo-con-imagen__imagen(src="@/assets/componentes/1.png")
            h3.titulo-con-imagen__texto Consideraciones clave para la desparasitacin
        
        p.mb-5 Debe realizarse siempre bajo criterio profesional, considerando:

        .row.justify-content-center
          .col-lg-10
            .bg-color-8.mb-5(data-aos="fade-up")
              .row.justify-content-center.align-items-center
                .col-lg-auto.mb-3.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema2/37.png', alt='')
                .col-lg.mb-3.mb-lg-0
                  .p-4
                    ul.lista-ul--color.mb-0
                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0 Especie y subespecie.
                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0  Edad y peso corporal.
                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0 Estado fisiolgico (crecimiento, gestacin, lactancia, vejez).
                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0 Condicin clnica y antecedentes sanitarios.
                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0  Tipo de alojamiento (urbano, rural, refugio, criadero).
                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0 Contacto con otros animales.
                      li.mb-0.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0 Presencia de vectores y condiciones climticas.
                      li.mb-0.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0 Riesgo epidemiolgico regional
        p.mb-5 El uso inadecuado de antiparasitarios puede causar intoxicaciones, resistencia parasitaria e incluso la muerte, especialmente en aves, roedores, lagomorfos y peces. A continuacin,  se presenta una descripcin de la endoparasitacin y la ectoparasitacin, integrando los tipos de parsitos involucrados, las vas de administracin y la frecuencia orientativa de control como parte de la medicina preventiva: 

        .row.align-items-start.mb-5
          .col-lg-3.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema2/38.png", alt="").mb-4.mb-lg-0
          .col-lg-9
            AcordionA(tipo="a" clase-tarjeta="tarjeta bg-color-6")
              .div(titulo="Endoparasitacin (parsitos internos)")
                p La endoparasitacin se orienta al control de parsitos que habitan sistemas internos del animal, como el digestivo, respiratorio, circulatorio, heptico o muscular. Estos organismos consumen nutrientes, lesionan tejidos y debilitan el sistema inmune. 
                p <b>Incluye</b>: nematodos (Toxocara spp., Ancylostoma spp., Trichuris spp.); cestodos (tenias como Dipylidium caninum); trematodos, menos comunes pero relevantes en zonas rurales; y protozoarios (Giardia spp., coccidios, Toxoplasma gondii). 
                ul.lista-ul--color.mb-0
                  li.mb-2.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0 Vas de administracin: oral (comprimidos, suspensiones, pastas), tpica sistmica (pipetas combinadas) e inyectable, solo bajo supervisin veterinaria. 
                  li.mb-2.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0  Frecuencia orientativa: cachorros y gatitos cada 15 a 30 das en los primeros meses; adultos cada 3 a 6 meses segn el riesgo; mascotas no convencionales nicamente bajo diagnstico y prescripcin veterinaria.
              .div(titulo="Ectoparasitacin (parsitos externos)")
                p La ectoparasitacin se enfoca en la eliminacin y prevencin de parsitos que habitan la piel, el pelo, las plumas o las escamas, generando dolor, estrs y enfermedades secundarias. 
                p <b>Incluye</b>: pulgas, garrapatas, piojos y caros. 
                ul.lista-ul--color.mb-0
                  li.mb-2.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0 Vas de administracin: tpica (pipetas, sprays, baos medicados), oral sistmica (comprimidos de accin prolongada) y ambiental (limpieza y desinfeccin del entorno). 
                  li.mb-2.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0 Frecuencia orientativa: aplicaciones mensuales o segn la duracin del producto, el nivel de infestacin y el riesgo ambiental, manteniendo siempre el control del entorno debido a que una proporcin elevada del ciclo de vida ocurre fuera del animal.
    
    p.mb-5 La desparasitacin responsable es parte de las obligaciones del tutor dentro de la tenencia responsable. Contribuye a la prevencin de zoonosis como toxocariasis, giardiasis, sarna y enfermedades transmitidas por vectores, conforme a los lineamientos del Ministerio de Salud, el ICA y la legislacin colombiana vigente.

    separador
    
    #t_2_4.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 2.4 Signos tempranos de enfermedad

    p.mb-5 La identificacin oportuna de los signos tempranos de enfermedad es una responsabilidad directa e intransferible del tutor y constituye un pilar fundamental de la prevencin, el bienestar animal y la salud pblica. Detectar estos signos a tiempo permite: 

    .row.justify-content-center
      .col-lg-10
        .bg-color-8.mb-5(data-aos="fade-up")
          .row.justify-content-center.align-items-center
            .col-lg-auto.mb-3.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/39.png', alt='')
            .col-lg.mb-3.mb-lg-0
              .p-4
                ul.lista-ul--color.mb-0
                  li.mb-3.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0 Intervencin veterinaria oportuna.
                  li.mb-3.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0  Mejor pronstico clnico.
                  li.mb-3.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0 Reduccin del sufrimiento animal.
                  li.mb-3.d-flex
                    i.fas.fa-circle-check.bg-color-3
                    p.mb-0 Disminucin del riesgo de transmisin de enfermedades.

    p.mb-5 A continuacin, se presentan los principales signos de alerta que permiten identificar posibles alteraciones en la salud de los animales de compaa, con nfasis en las particularidades de las mascotas no convencionales.
    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-white.tarjeta.p-4
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/40.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Signos en mascotas convencionales
              p Pueden evidenciar alteraciones en el estado de salud de cualquier animal de compaa. Incluyen disminucin del apetito o del consumo de agua, letargo, apata o debilidad, cambios bruscos de comportamiento, prdida de peso, secreciones anormales en ojos, nariz, boca, odos o genitales, dificultad respiratoria, tos o estornudos persistentes, alteraciones en heces u orina, lesiones cutneas, cada anormal de pelo o plumaje, prurito, cojeras, inflamaciones, dolor al movimiento, aislamiento y disminucin de la interaccin social.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema2/41.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              h5.mb-5 Signos en mascotas no convencionales
              p En aves, roedores, lagomorfos, reptiles y peces, los signos de enfermedad suelen ser sutiles y progresivos, debido a la tendencia natural a ocultar el malestar. Cualquier cambio debe considerarse seal de alerta, incluso si es leve. Se manifiestan principalmente como alteraciones en la conducta, la postura, el consumo de alimento, la coloracin, el nivel de actividad y el aspecto general.
    
    .bg-full-width.bg-color-4
      .px-4.px-md-5
        .row.justify-content-center
          .col-lg-9
            .titulo-sexto.color-acento-contenido
              h5 Tabla 4.
              span Comparativo de observacin por tipo de mascota
            
            .tabla-a.color-acento-contenido.mb-5.tabl
              table
                thead
                  tr
                    th.bg-color-9 Tipo de mascota
                    th.bg-color-9 Cambios que deben alertar al tutor
                tbody
                  tr
                    td Perros y gatos
                    td Prdida de apetito, letargo, tos, vmito, diarrea, secreciones, cambios de conducta.
                  tr.bg-color-sintesis
                    td Aves
                    td Plumaje erizado, silencio inusual, descenso de actividad, cambios en las heces.
                  tr
                    td Roedores y lagomorfos
                    td Disminucin en el consumo, inmovilidad, pelaje desarreglado, respiracin agitada.
                  tr.bg-color-sintesis
                    td Reptiles
                    td Falta de termorregulacin, coloracin anormal, prdida de peso, inactividad.
                  tr
                    td Peces ornamentales
                    td Nado errtico, prdida de color, aletas plegadas, aislamiento, falta de apetito.

        
        p.mb-5 La observacin diaria, el conocimiento del comportamiento normal y el registro de cambios son herramientas clave del tutor responsable. Cualquier anomala debe motivar una consulta veterinaria inmediata.

        separador
        
        #t_2_5.titulo-segundo.color-acento-contenido(data-aos="flip-up")
          h2 2.5 Medidas de bioseguridad
        
        .row.mb-5
          .col-lg-5.order-lg-2.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema2/42.png", data-aos="zoom-in")
          .col-lg-7.order-lg-1
            p(data-aos="fade-left") Las medidas de bioseguridad comprenden el conjunto de prcticas, protocolos y hbitos preventivos orientados a evitar la introduccin, diseminacin y transmisin de agentes patgenos (bacterias, virus, parsitos, hongos y otros microorganismos) dentro del hogar, entre animales y hacia la comunidad. Su aplicacin sistemtica es esencial tanto en hogares con una sola mascota como en aquellos con mltiples animales, especies diferentes o en condiciones de mayor riesgo sanitario.
        
            .bg-color-7.tarjeta.p-4(data-aos="fade-left")
              p.mb-0 La bioseguridad constituye un pilar fundamental de la medicina preventiva, ya que permite reducir la incidencia de enfermedades infecciosas y parasitarias, minimizar brotes sanitarios, proteger la salud de los tutores y fortalecer la convivencia responsable. Estas medidas no solo protegen a la mascota, sino que tambin disminuyen el riesgo de zoonosis, previenen la contaminacin ambiental y consolidan el enfoque de Una Sola Salud (One Health), que reconoce la estrecha relacin entre la salud animal, humana y ambiental.

        p.mb-5 En Colombia, la implementacin de estas medidas forma parte de las obligaciones del tutor dentro de la tenencia responsable, especialmente en contextos de riesgo como la introduccin de nuevos animales, la presencia de enfermedades, la convivencia con fauna silvestre o la ocurrencia de eventos sanitarios de inters en salud pblica.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.png")
        h3.titulo-con-imagen__texto Principios generales de la bioseguridad en el hogar
    
    p.mb-5 Estos principios orientan la aplicacin de las prcticas bsicas de bioseguridad y garantizan su efectividad en distintos contextos:

    .row.mb-5
      .col-lg-4.order-lg-2.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema2/43.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        TabsA.color-acento-botones.mb-5
          //- .tarjeta debe ir acompaado de una de una de estas clases => 
          //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones
          //- estas clases tambien tienen un modificador --borde
          .tarjeta.color-acento-botones--borde.p-4(titulo="Prevencin")
            h4 Prevencin
            p Evitar el ingreso de agentes infecciosos al entorno domstico.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Contencin")
            h4 Contencin
            p Limitar la diseminacin de patgenos cuando existe un animal enfermo o sospechoso.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Proteccin")
            h4 Proteccin
            p Salvaguardar la salud del animal, del tutor y de la comunidad.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Responsabilidad")
            h4 Responsabilidad
            p Aplicacin constante y consciente de las prcticas sanitarias.

    .bg-full-width.bg-color-12
      .px-4.px-md-5
        .titulo-con-imagen.mb-5(data-aos="fade-right")
          .titulo-con-imagen__fondo
            img.titulo-con-imagen__imagen(src="@/assets/componentes/1.png")
            h3.titulo-con-imagen__texto Medidas bsicas de bioseguridad aplicables a todas las especies
        
        p.mb-5 A continuacin, se describen las acciones esenciales que deben aplicarse de forma sistemtica en cualquier hogar con animales de compaa:

        .bg-full-width.bg-fondo-slider.mb-5
          .p-4.p-md-5
            SlyderA(tipo="b").bg-white.tarjeta.p-4
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/44.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Lavado de manos
                  p Lavado riguroso con agua y jabn antes y despus de manipular animales, alimentos, jaulas, areneros, acuarios, utensilios o residuos. Esta medida resulta clave para prevenir la transmisin de zoonosis y debe reforzarse especialmente en nios, adultos mayores y personas inmunosuprimidas.
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/45.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Limpieza y desinfeccin del entorno
                  p Limpieza peridica de camas, platos, bebederos, jaulas, acuarios, transportadoras, areneros y reas de descanso, utilizando productos seguros y no txicos para cada especie. La frecuencia se ajusta al nmero de animales y al nivel de riesgo sanitario.
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/46.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Cuarentena preventiva
                  p Aplicacin de aislamiento para animales nuevos, rescatados o provenientes de criaderos, ferias o con antecedentes sanitarios desconocidos. Durante este perodo debe evitarse el contacto con otras mascotas y el uso compartido de utensilios hasta completar la evaluacin veterinaria.
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/47.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Manejo adecuado de excretas y residuos
                  p Recoleccin diaria y disposicin adecuada de heces, orina, restos de alimento, material de cama y residuos biolgicos. Se busca evitar la acumulacin de desechos, la contaminacin de fuentes de agua y el acceso de insectos o roedores.
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/48.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Control de contactos de riesgo
                  p Prevencin del contacto de la mascota con animales silvestres, fauna callejera, animales enfermos o de origen desconocido, que pueden actuar como portadores de agentes infecciosos o parasitarios.
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/49.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Uso responsable de medicamentos veterinarios
                  p Administracin exclusiva de medicamentos prescritos por un profesional, respetando dosis, va de administracin, frecuencia y duracin del tratamiento. El uso inadecuado puede generar resistencia antimicrobiana, intoxicaciones y fallos teraputicos.
              .row.align-items-center.p-4.p-md-5
                .col-lg-5.order-2
                  figure
                    img(src="@/assets/curso/tema2/50.png")
                .col-lg-7.order-1.mb-3.mb-lg-0
                  h5.mb-5 Prohibicin de la automedicacin
                  p Nunca utilizar medicamentos humanos ni productos no autorizados para uso veterinario, ya que algunas sustancias resultan altamente txicas o letales, especialmente en gatos, aves, conejos y cobayos.
              .row.align-items-center.p-4.p-md-5
