                      li.mb-3.d-flex
                        i.fas.fa-circle-check.bg-color-3
                        p.mb-0  Aislamiento inmediato ante signos de enfermedad y consulta veterinaria oportuna.

        p.mb-5 Estas prcticas ayudan a reducir significativamente la transmisin de enfermedades como rabia, leptospirosis, parasitosis y enfermedades respiratorias.

