        #t_2_6.titulo-segundo.color-acento-contenido(data-aos="flip-up")
          h2 2.6 Salud pblica
        
        p.mb-5 La salud animal est estrechamente vinculada a la salud pblica, ya que las mascotas pueden actuar como reservorios, vectores o vctimas de agentes infecciosos con capacidad de afectar a las personas, a otros animales y al ambiente. En este sentido, la gestin sanitaria responsable de las mascotas constituye un componente estratgico de la salud pblica, especialmente en contextos urbanos y periurbanos donde la convivencia humanoanimal es permanente.

        .row.mb-5
          .col-lg-5.order-lg-2.mb-3.mb-lg-0
            figure
              img(src="@/assets/curso/tema2/58.png", data-aos="zoom-in")
          .col-lg-7.order-lg-1  
            .bg-color-15.tarjeta.p-5(data-aos="fade-left").mb-3
              p.mb-0 Bajo el enfoque de Una Sola Salud (One Health), reconocido por organismos internacionales y adoptado progresivamente en Colombia, se entiende que la salud humana, la salud animal y la salud ambiental son interdependientes. Por tanto, la prevencin y el control de enfermedades no pueden abordarse de manera aislada, sino mediante acciones coordinadas entre tutores, profesionales veterinarios, autoridades sanitarias y la comunidad.
            p(data-aos="fade-left") El incumplimiento de las obligaciones sanitarias en mascotas, ya sea por negligencia, desconocimiento o falta de control, puede generar riesgos colectivos, favorecer la aparicin de brotes zoonticos, incrementar los costos en salud pblica y afectar la convivencia social.

        .titulo-con-imagen.mb-5(data-aos="fade-right")
