            img.titulo-con-imagen__imagen(src="@/assets/componentes/1.png")
            h3.titulo-con-imagen__texto Impacto del manejo sanitario de las mascotas en la salud pblica

        p.mb-5 A continuacin, se presenta un pdcast que aborda el impacto del manejo sanitario de las mascotas en la salud pblica, destacando su rol clave en la prevencin de zoonosis, la promocin del bienestar colectivo y el fortalecimiento de la vigilancia epidemiolgica.

        .row.mb-5.justify-content-center.align-items-center
