                    TarjetaAudio.color-acento-botones.bg-color-white.mb-3(
                      texto="Impacto del manejo sanitario de las mascotas en la salud pblica"
                      tiempo
                      :audio="require('../assets/curso/podcast/podcast1.mp3')"
                    )

        p.mb-5 La relacin entre mascotas y salud pblica exige prcticas de cuidado responsables que protejan tanto el bienestar animal como la seguridad sanitaria de la comunidad.

    .row.align-items-start.mb-5
      .col-lg-3.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema2/61.png", alt="").mb-4.mb-lg-0
      .col-lg-9
        AcordionA(tipo="a" clase-tarjeta="tarjeta bg-color-6")
          .div(titulo="Salud pblica y mascotas convencionales")
            p En perros y gatos, la relacin con la salud pblica es especialmente relevante debido a su alta densidad poblacional y estrecha convivencia con los humanos. Las principales acciones con impacto sanitario directo incluyen:
            ul.lista-ul--color.mb-0
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Vacunacin obligatoria contra la rabia
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0  Desparasitacin regular con enfoque zoontico
              li.mb-0.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Control de ectoparsitos transmisores de enfermedades
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Manejo responsable en espacios pblicos
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Prevencin de mordeduras mediante educacin, socializacin y atencin veterinaria
            p Estas medidas permiten reducir significativamente la incidencia de zoonosis urbanas y los eventos de riesgo para la comunidad.
          .div(titulo="Salud pblica y mascotas no convencionales")
            p En especies como aves, cobayos, conejos, reptiles y peces ornamentales, los riesgos sanitarios son ms especficos y menos evidentes. A continuacin, se resumen las principales medidas de prevencin:
            ul.lista-ul--color.mb-0
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Control sanitario estricto del origen del animal
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Cuarentena y observacin clnica
              li.mb-0.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Medidas de bioseguridad reforzadas
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Manejo higinico de jaulas, aviarios, acuarios y terrarios
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Educacin del tutor sobre riesgos zoonticos asociados a cada especie
            p Enfermedades como salmonelosis, psitacosis, dermatofitosis y diversas parasitosis pueden transmitirse por contacto directo o indirecto si no se aplican las medidas adecuadas.
          .div(titulo="Responsabilidades del tutor frente a la salud pblica")
            p El tutor de una mascota, ya sea convencional o no convencional, tiene la responsabilidad legal y social de participar activamente en las siguientes acciones:
            ul.lista-ul--color.mb-0
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Campaas oficiales de vacunacin, especialmente contra la rabia, promovidas por las autoridades sanitarias.
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0  Programas de esterilizacin y control poblacional, como medida preventiva y de bienestar
              li.mb-0.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Jornadas de educacin sanitaria y bienestar animal.
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 Procesos de notificacin obligatoria ante la sospecha o confirmacin de enfermedades de control oficial.
            p Cumplimiento de la normativa sanitaria vigente, evitando prcticas que pongan en riesgo la salud colectiva.
          .div(titulo="Dimensin legal y social")
            p El cumplimiento de las acciones de salud pblica asociadas a la tenencia de mascotas no es opcional. En Colombia, estas prcticas se enmarcan en tres principios fundamentales:
            ul.lista-ul--color.mb-0
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 El reconocimiento de los animales como seres sintientes
              li.mb-2.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 La obligacin del tutor de garantizar condiciones sanitarias adecuadas
              li.mb-0.d-flex
                i.fas.fa-circle-check.bg-color-3
                p.mb-0 La proteccin del inters general y la salud colectiva
            p El incumplimiento puede dar lugar a sanciones administrativas, responsabilidades civiles o penales, especialmente cuando se compromete la salud pblica.
        
